import React, { useState, useEffect } from 'react';
import { Typography, Box, Grid, CircularProgress } from '@mui/material';
import { Line } from 'react-chartjs-2';
import { format, subDays } from 'date-fns';
import axios from 'axios';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
  ChartOptions
} from 'chart.js';

// Register ChartJS components
ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend
);

interface RatesData {
  timestamp: number;
  rates: {
    EUR: number;
    USD: number;
  };
  history: Array<{
    timestamp: number;
    EUR: number;
    USD: number;
  }>;
}

interface ChartDataset {
  label: string;
  data: number[];
  borderColor: string;
  backgroundColor: string;
}

interface ChartData {
  labels: string[];
  datasets: ChartDataset[];
}

const CurrencyRates: React.FC = () => {
  console.log('CurrencyRates rendering');

  const [rates, setRates] = useState<RatesData | null>(null);
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    console.log('CurrencyRates useEffect running');
    const fetchRates = async () => {
      try {
        //       
        const response = await axios.get('https://www.cbr-xml-daily.ru/daily_json.js');
        const currentRates = {
          EUR: response.data.Valute.EUR.Value,
          USD: response.data.Valute.USD.Value
        };

        //      7 
        const historyData = [];
        for (let i = 1; i <= 7; i++) {
          const date = subDays(new Date(), i);
          const formattedDate = format(date, 'yyyy/MM/dd');
          try {
            const historyResponse = await axios.get(`https://www.cbr-xml-daily.ru/archive/${formattedDate}/daily_json.js`);
            historyData.push({
              timestamp: date.getTime(),
              EUR: historyResponse.data.Valute.EUR.Value,
              USD: historyResponse.data.Valute.USD.Value
            });
          } catch (err) {
            console.warn(`Failed to fetch data for ${formattedDate}`, err);
          }
        }

        //      
        historyData.unshift({
          timestamp: Date.now(),
          EUR: currentRates.EUR,
          USD: currentRates.USD
        });

        const ratesData: RatesData = {
          timestamp: Date.now(),
          rates: currentRates,
          history: historyData
        };

        console.log('Real data fetched:', ratesData);
        setRates(ratesData);
        setLoading(false);
      } catch (error) {
        console.error('Error fetching currency rates:', error);
        setError('Failed to load currency rates');
        setLoading(false);
      }
    };

    fetchRates();
    const interval = setInterval(fetchRates, 60000);
    return () => clearInterval(interval);
  }, []);

  if (error) {
    return <Typography color="error">{error}</Typography>;
  }

  if (loading || !rates) {
    return (
      <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: '100%' }}>
        <CircularProgress />
      </Box>
    );
  }

  const chartData: ChartData = {
    labels: rates.history.map(entry => format(new Date(entry.timestamp), 'dd.MM HH:mm')),
    datasets: [
      {
        label: 'EUR/RUB',
        data: rates.history.map(entry => entry.EUR),
        borderColor: 'rgb(75, 192, 192)',
        backgroundColor: 'rgba(75, 192, 192, 0.5)',
      },
      {
        label: 'USD/RUB',
        data: rates.history.map(entry => entry.USD),
        borderColor: 'rgb(153, 102, 255)',
        backgroundColor: 'rgba(153, 102, 255, 0.5)',
      }
    ],
  };

  console.log('Chart data prepared:', chartData);

  const chartOptions: ChartOptions<'line'> = {
    responsive: true,
    maintainAspectRatio: false,
    plugins: {
      legend: {
        position: 'top',
      },
      title: {
        display: true,
        text: 'Currency Rates vs RUB (7 days)',
      },
    },
    scales: {
      y: {
        type: 'linear',
        beginAtZero: false,
      },
    },
  };

  return (
    <Box sx={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
      <Grid container spacing={2} alignItems="center" sx={{ mb: 2 }}>
        <Grid item xs={6}>
          <Typography variant="h6" color="primary">
            EUR/RUB: {rates.rates.EUR.toFixed(2)}
          </Typography>
        </Grid>
        <Grid item xs={6}>
          <Typography variant="h6" color="secondary">
            USD/RUB: {rates.rates.USD.toFixed(2)}
          </Typography>
        </Grid>
      </Grid>
      <Box sx={{ flexGrow: 1, position: 'relative' }}>
        <Line options={chartOptions} data={chartData} />
      </Box>
    </Box>
  );
};

export default CurrencyRates; 