import React, { useState, useEffect } from 'react';
import { Typography, Box, Grid, CircularProgress } from '@mui/material';
import { Line } from 'react-chartjs-2';
import { format } from 'date-fns';
import axios from 'axios';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
  ChartOptions
} from 'chart.js';
import TimeRangeSlider from './TimeRangeSlider';

// Register ChartJS components
ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend
);

interface StatsData {
  timestamp: number;
  current: {
    steam: number;
    ea: number;
    total: number;
  };
  history: Array<{
    timestamp: number;
    steam: number;
    ea: number;
    total: number;
  }>;
}

const TRACKER_API_KEY = 'YOUR_API_KEY'; //      tracker.gg

const ApexStats: React.FC = () => {
  const [stats, setStats] = useState<StatsData | null>(null);
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<string | null>(null);
  const [timeRange, setTimeRange] = useState<number>(7);

  useEffect(() => {
    const fetchStats = async () => {
      try {
        setLoading(true);
        
        //    Steam  Steam Web API
        const steamResponse = await axios.get(
          'https://api.steampowered.com/ISteamUserStats/GetNumberOfCurrentPlayers/v1/?appid=1172470'
        );
        
        //     tracker.gg API
        const trackerResponse = await axios.get(
          'https://public-api.tracker.gg/v2/apex/standard/stats',
          {
            headers: {
              'TRN-Api-Key': TRACKER_API_KEY
            }
          }
        );

        //    EA    Steam/EA
        const steamPlayers = steamResponse.data.response.player_count;
        const eaPlayers = Math.round(steamPlayers * 0.7); //  
        const totalPlayers = steamPlayers + eaPlayers;

        //       tracker.gg
        const history = trackerResponse.data.data.map((entry: any) => ({
          timestamp: new Date(entry.timestamp).getTime(),
          steam: entry.stats.steamPlayers,
          ea: entry.stats.originPlayers,
          total: entry.stats.totalPlayers
        }));

        const statsData: StatsData = {
          timestamp: Date.now(),
          current: {
            steam: steamPlayers,
            ea: eaPlayers,
            total: totalPlayers
          },
          history
        };

        setStats(statsData);
        setLoading(false);
      } catch (error) {
        console.error('Error fetching Apex Legends stats:', error);
        setError('Failed to load Apex Legends stats');
        setLoading(false);
      }
    };

    fetchStats();
    const interval = setInterval(fetchStats, 300000); //   5 
    return () => clearInterval(interval);
  }, []);

  if (error) {
    return <Typography color="error">{error}</Typography>;
  }

  if (loading || !stats) {
    return (
      <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: '100%' }}>
        <CircularProgress />
      </Box>
    );
  }

  const chartData = {
    labels: stats.history
      .slice(0, timeRange)
      .map(entry => format(new Date(entry.timestamp), 'dd MMM')),
    datasets: [
      {
        label: 'Steam Players',
        data: stats.history
          .slice(0, timeRange)
          .map(entry => entry.steam),
        borderColor: 'rgb(255, 99, 132)',
        backgroundColor: 'rgba(255, 99, 132, 0.5)',
      },
      {
        label: 'EA Players',
        data: stats.history
          .slice(0, timeRange)
          .map(entry => entry.ea),
        borderColor: 'rgb(53, 162, 235)',
        backgroundColor: 'rgba(53, 162, 235, 0.5)',
      },
      {
        label: 'Total Players',
        data: stats.history
          .slice(0, timeRange)
          .map(entry => entry.total),
        borderColor: 'rgb(75, 192, 192)',
        backgroundColor: 'rgba(75, 192, 192, 0.5)',
      }
    ],
  };

  const chartOptions: ChartOptions<'line'> = {
    responsive: true,
    maintainAspectRatio: false,
    plugins: {
      legend: {
        position: 'top',
      },
      title: {
        display: true,
        text: 'Apex Legends Player Count',
      },
    },
    scales: {
      y: {
        type: 'linear',
        beginAtZero: false,
        ticks: {
          callback: function(this: any, value: number | string) {
            const numValue = typeof value === 'string' ? parseFloat(value) : value;
            return `${(numValue / 1000).toFixed(0)}k`;
          }
        }
      },
    },
  };

  return (
    <Box sx={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
      <Grid container spacing={2} alignItems="center" sx={{ mb: 2 }}>
        <Grid item xs={4}>
          <Typography variant="h6" color="primary">
            Steam: {(stats.current.steam / 1000).toFixed(1)}k
          </Typography>
        </Grid>
        <Grid item xs={4}>
          <Typography variant="h6" color="secondary">
            EA: {(stats.current.ea / 1000).toFixed(1)}k
          </Typography>
        </Grid>
        <Grid item xs={4}>
          <Typography variant="h6" color="success">
            Total: {(stats.current.total / 1000).toFixed(1)}k
          </Typography>
        </Grid>
      </Grid>

      <TimeRangeSlider
        value={timeRange}
        onChange={setTimeRange}
        min={1}
        max={Math.min(30, stats.history.length)}
      />

      <Box sx={{ flexGrow: 1, position: 'relative', mt: 2 }}>
        <Line options={chartOptions} data={chartData} />
      </Box>
    </Box>
  );
};

export default ApexStats; 